import React, {useState, ChangeEvent, SyntheticEvent} from 'react';
import { useSelector, useDispatch } from '../services/hooks';
import { Link, Redirect, useLocation } from 'react-router-dom';
import { Button, Input, PasswordInput } from '@ya.praktikum/react-developer-burger-ui-components';
import CellEmpty from '../components/cell-empty/cell-empty';
import { authorize } from '../services/actions';
import styles from './login.module.css';
import { ILocation } from '../utils/types';

function LoginPage() {
  const [form, setValue] = useState({ email: '', password: '' });
  const location = useLocation<ILocation>();
  const dispatch = useDispatch();
  const user = useSelector(store => store.user.user);

  const onChange = (e: ChangeEvent<HTMLInputElement>) => {
    setValue({ ...form, [e.target.name]: e.target.value });
  }
  function handleSubmit(e: SyntheticEvent) {
    e.preventDefault();
    const {email, password} = form;
    dispatch(authorize(email, password));
  }

  if (user.name !=='') {
    return <Redirect to={location.state?.from || '/'}/>
  }

  return (
    <div className={styles.login}>
      <h1 className="text text_type_main-medium mb-6"></h1>
      <form className={styles.form} onSubmit={handleSubmit} noValidate>
        <Input
          type={'email'}
          placeholder={'E-mail'}
          onChange={onChange}
          value={form.email}
          name={'email'}
          error={false}
          errorText={''}
          size={'default'}
        />
        <CellEmpty height="pt-6"/>
        <PasswordInput size="default" onChange={onChange} value={form.password} name={'password'} />
        <CellEmpty height="pt-6"/>
        <Button type="primary" size="medium"></Button>
      </form>
      <p className="text text_type_main-default text_color_inactive mt-20 mb-4">   ?
        <Link to="/register" className={`text text_type_main-default ${styles.link}`}></Link>
      </p>
      <p className="text text_type_main-default text_color_inactive"> ?
        <Link to="/forgot-password" className={`text text_type_main-default ${styles.link}`}> </Link>
      </p>
    </div>
  );
}

export default LoginPage;
